:root {
  --primary: #f53582;
  --secondary: #0f6e8a;
  --background: #45badd;
}

html {
  box-sizing: border-box;
}

body {
  background: var(--background);
  margin: 0;
  font-family: Courier New, Courier, monospace;
}

.container {
  background: url("robot.e5d204be.gif") 0 / contain no-repeat;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

button {
  cursor: pointer;
  color: var(--primary);
  background: #fff;
  border: none;
  border-radius: 5px;
  outline: none;
  width: 100%;
  height: 50px;
  font-size: 1.25rem;
  box-shadow: 2px 2px 20px 10px #0003;
}

button:hover {
  filter: brightness(95%);
}

button:active {
  transform: scale(.98);
}

button:disabled {
  cursor: default;
  filter: brightness(30%);
}

.controls {
  background-color: var(--primary);
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  gap: 32px;
  width: 393px;
  height: auto;
  padding: 2rem;
  display: flex;
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translate(-50%, -50%);
  box-shadow: 2px 2px 20px 10px #0003;
}

.controls-title {
  text-align: center;
  color: #fff;
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 2rem;
  font-weight: bold;
}

hr {
  background-color: #fff;
  border: none;
  width: 100%;
  height: 2px;
}

.controls-settings {
  background-color: var(--secondary);
  border-radius: 10px;
  justify-content: center;
  align-self: stretch;
  align-items: center;
  gap: 1rem;
  padding: .5rem .5rem 1rem;
  display: flex;
}

.settings-nsfw {
  background: #fff;
  border-radius: 6px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  width: 116px;
  padding: 8px 4px;
  display: flex;
  overflow: hidden;
}

.settings-nsfw__label {
  color: var(--primary);
  letter-spacing: -.16px;
  text-align: center;
  align-self: stretch;
  font-size: 1rem;
  font-style: normal;
  font-weight: 600;
  line-height: 1.5rem;
}

.switch {
  width: 60px;
  height: 34px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  background-color: #fff;
  border-radius: 50%;
  width: 26px;
  height: 26px;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: var(--primary);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--primary);
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.settings-nsfw__subtitle {
  color: var(--primary);
  letter-spacing: -.16px;
  text-align: center;
  align-self: stretch;
  font-size: .875rem;
  font-weight: 600;
  line-height: 1.5rem;
}

.settings-categories {
  flex-direction: column;
  flex: 1 0 0;
  justify-content: center;
  align-self: stretch;
  align-items: flex-start;
  gap: .5rem;
  padding: .5rem;
  display: flex;
}

.settings-categories__label {
  color: #fff;
  letter-spacing: -.14px;
  font-size: 1rem;
  font-weight: 600;
  line-height: 1rem;
  display: flex;
}

.settings-categories__inputgroup {
  flex-direction: column;
  align-items: flex-start;
  gap: .5rem;
  display: flex;
}

.settings-categories__inputgroup label {
  cursor: pointer;
  color: #fff;
  letter-spacing: -.16px;
  -webkit-user-select: none;
  user-select: none;
  padding-left: 2rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5rem;
  position: relative;
}

.settings-categories__inputgroup label input {
  cursor: pointer;
  opacity: 0;
  width: 0;
  height: 0;
  position: absolute;
}

label:hover input ~ .checkmark {
  background-color: #ccc;
}

label input:checked ~ .checkmark {
  background-color: var(--primary);
}

.checkmark {
  background-color: #fff;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 0;
  left: 0;
}

.checkmark:after {
  content: "";
  display: none;
  position: absolute;
}

input:checked ~ .checkmark:after {
  display: block;
}

.checkmark:after {
  border: 3px solid #fff;
  border-width: 0 3px 3px 0;
  width: 5px;
  height: 10px;
  top: 5px;
  left: 9px;
  transform: rotate(45deg);
}

footer {
  color: #fff;
  letter-spacing: -.16px;
  text-align: center;
  font-size: .75rem;
  font-weight: 600;
  line-height: 1.5rem;
  position: fixed;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (width <= 768px) {
  .container {
    background: none;
  }

  .controls {
    width: clamp(250px, 80%, 350px);
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
